
.toast[data-v-bef99986] {
	height: 40px;
	line-height: 40px;
	padding: 0 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: fixed;
	top: 50%;
	left: 50%;
	margin-top: -20px;
	text-align: center;
	/*background: rgba(0, 0, 0, .4);*/
	border-radius: 10px;
	background: #e1e1e1;
	font-size: 14px;
	color: #333;
	-webkit-transform: translate(-50%);
	transform: translate(-50%);
	z-index: 111;
}



