
* {
  padding: 0 0;
  margin: 0 0;
  outline: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html,
body {
  background: #f6f7fb;
  font: 14px "Microsoft YaHei", "微软雅黑", sans-serif;
}
a {
  text-decoration: none;
  color: inherit;
  outline: 0;
}
img {
  border: none;
  vertical-align: middle;
}
ul,
li {
  list-style: none;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
.background-icon {
  background: url("./common/img/icon.png") no-repeat;
}
body > div > footer {
  margin-top: 40px;
  width: 100%;
  height: 100px;
  background: #272a2c;
}
body > div > footer > div {
  width: 1200px;
  height: 100px;
  line-height: 100px;
  margin: 0 auto;
  color: #fff;
}
body > div > footer > div span {
  display: inline-block;
  font-size: 14px;
  color: #fff;
  margin-right: 140px;
}
body > div > footer > div p {
  max-width: 800px;
}
body > div > footer > div p a {
  font-size: 14px;
  color: #fff;
  margin-left: 20px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.hide {
  display: none;
}
.curr {
  display: block;
}
.rmyx {
  width: 312px;
}
.rmyx div > h2 {
  position: relative;
  height: 22px;
  font-size: 22px;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: 0px;
  color: #343434;
  padding-left: 15px;
}
.rmyx div > h2:before {
  content: "";
  position: absolute;
  left: 0;
  width: 6px;
  height: 22px;
  background-color: #e75208;
}
.rmyx div > ul {
  width: 100%;
  margin-top: 20px;
}
.rmyx div > ul li {
  width: 312px;
  height: 200px;
  margin-top: 20px;
}
.rmyx div > ul li a {
  display: block;
  width: 312px;
  height: 200px;
  overflow: hidden;
  position: relative;
}
.rmyx div > ul li a img {
  width: 312px;
  height: 200px;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  -ms-transition: 1s;
}
.rmyx div > ul li a > div.lay {
  background: none;
}
.rmyx div > ul li a:hover > div.lay {
  width: 100%;
  height: 200px;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.1);
}
.rmyx div > ul li a:hover img {
  transform: scale(1.43);
  -webkit-transform: scale(1.43);
  -moz-transform: scale(1.43);
  -o-transform: scale(1.43);
  -ms-transform: scale(1.43);
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  -ms-transition: 1s;
}
.fid {
  position: fixed;
  top: 110px;
  margin: 0 0 !important;
  z-index: 9;
}
.fid > ul > li:not(:nth-of-type(-n+2)) {
  display: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
/*margin-top: 60px;*/
}


